@import './../../node_modules/code-prettify/styles/sons-of-obsidian.css';

@import 'base/variable.scss';

.nav-tabs {
    width: $default_width;
    float: left;
    margin: 0;
    list-style-type: none;
}

.nav-tabs > li {
    float: left;
    margin-bottom: -1px;
}

.nav-tabs > li > a {
    text-decoration: none;
    line-height: 1.2;
    padding: 1em;
    float: left;
}

.nav-tabs > li > a:hover {
    border-color: #eee #eee #ddd;
}

.nav-tabs > li.active a ,
.nav-tabs > li.active a:hover,
.nav-tabs > li.active a:focus {
    color: black;
    background-color: #fff;
    cursor: pointer;
    border: 1px solid $border_color;
}

.tab-content > .tab-pane {
    float: left;
    width: $default_width;
    display: none;
}

.tab-content > .tab-pane.active {
    display: block;
    padding: 12px;
    background-color: #fff;

}

.tableCptManager {
    width: 100%;

} 

.tableCptManager td{
    border: 1px solid;
    padding: 10px;

} 

.tableCptManager th {
    border: 1px solid;
    background-color: #ddd;
    text-align: left;
    padding: 10px;
}


#submit_edit {
    color: white;
    background-color: blue;
    font-weight: bold;
    // padding: 10px 20px 10px 20px;  // top, right, bottom, left
    border-radius: 10em;
    text-decoration: none;
}

#submit_delete {
    color: white;
    background-color: red;
    font-weight: bold;
    border-radius: 10em;
    text-decoration: none;
}

.noCustomPostType {
    text-align: center;
}